import React, { useState, useEffect, useRef } from 'react';
import LoginBox from './LoginBox';
import SignUpBox from './SignUpBox';
import EmailBox from './EmailBox';
import Navbar from '../components/Navbar';
import PasswordBox from './PasswordBox';


function App() {
  const [showLoginBox, setShowLoginBox] = useState(false);
  const [showSignUpBox, setShowSignUpBox] = useState(false);
  const [showEmailBox, setShowEmailBox] = useState(false);
  const [showPasswordBox, setShowPasswordBox] = useState(false);

  const closeAllPopups = () => {
    setShowLoginBox(false);
    setShowSignUpBox(false);
    setShowEmailBox(false);
    setShowPasswordBox(false);
  };

  const showSignUp = () => {
    closeAllPopups();
    setShowSignUpBox(true);
  };

  const showEmail = () => {
    closeAllPopups();
    setShowEmailBox(true);
  };

  const showPassword = () => {
    closeAllPopups();
    setShowPasswordBox(true);
  };

  const handleLoginButtonClick = () => {
    closeAllPopups();
    setShowLoginBox(true);
  };

  const popupRef = useRef(null);

  useEffect(() => {
    const handleKeyPress = (event) => {
      if (event.key === 'Escape') {
        closeAllPopups();
      }
    };

    const handleClickOutside = (event) => {
      if (
        popupRef.current &&
        !popupRef.current.contains(event.target) &&
        event.target.getAttribute('id') !== 'loginButtonNav'
      ) {
        closeAllPopups();
      }
    };
    

    window.addEventListener('keydown', handleKeyPress);
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      window.removeEventListener('keydown', handleKeyPress);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <div>
      <Navbar onLoginButtonClick={handleLoginButtonClick} />

      <div ref={popupRef}>
        {showLoginBox && (
          <LoginBox
            showSignUpBox={showSignUp}
            showEmailBox={showEmail}
            showPasswordBox={showPassword}
          />
        )}
   {showSignUpBox && <SignUpBox onGoBack={handleLoginButtonClick} onSignUpSuccess={closeAllPopups} />}
   {showSignUpBox && <SignUpBox onGoBack={handleLoginButtonClick} showPasswordBox={showPassword} onLoginSuccess={closeAllPopups} />}

        {showPasswordBox && (
          <PasswordBox onGoBack={handleLoginButtonClick} />
        )}
      </div>
    </div>
  );
}

export default App;