import React, { useState } from 'react';
import { auth } from '../firebase';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { AuthErrorCodes } from 'firebase/auth';

function SignUpBox({ onGoBack,onSignUpSuccess }) {
  const [email, setEmail] = useState(''); // Declare and initialize email state
  const [password, setPassword] = useState(''); // Declare and initialize password state
  const [error, setError] = useState(null); // Declare and initialize error state

  const handleSignUp = () => {
    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // User signed up successfully
        const user = userCredential.user;
        console.log('Signed up successfully:', user);
        
        // Call the callback function to close the SignUpBox
        if (onSignUpSuccess) {
          onSignUpSuccess();
        }
      })
      .catch((error) => {
        // Handle signup error
        const errorCode = error.code;
        let errorMessage = error.message;
  
        // Customize error messages based on error codes
        switch (errorCode) {
          case AuthErrorCodes.INVALID_EMAIL:
            errorMessage = 'Invalid email address';
            break;
          case AuthErrorCodes.WEAK_PASSWORD:
            errorMessage = 'Password is too weak';
            break;
          case AuthErrorCodes.EMAIL_ALREADY_IN_USE:
            errorMessage = 'Email address is already in use';
            break;
          // Add more cases as needed for other error codes
          default:
            errorMessage = 'An error occurred during sign-up';
        }
  
        setError(errorMessage);
      });
  };

  const handlePasswordInputKeyDown = (event) => {
    if (event.key === 'Enter') {
      // If Enter key is pressed in the password input field, submit the form
      handleSignUp();
    }
  };

  return (
<div>
      <div className="login-box" id="signupBox">
        <div className="log-in-text">
          <div className="opening-second">
            <div className="go-back">
            <button className="go-back-button" onClick={onGoBack}>
                <i className="fas fa-backspace"></i>
              </button>
              <div className="center-text">
                <h4 className="log-in-text-heading">Sign-Up</h4>
              </div>
            </div>
          </div>
          <h6 className="log-in-text-closing">Let's sign you up so you can get the answers you need!</h6>
        </div>
        <form id="signupForm">
          <div className="signup-form">
            <div className="col-lg-12 col-md mb-3">
              <input type="text" placeholder="First Name" id="input-FirstName" className="shadow form-control form-control-lg" />
            </div>
            <div className="col-lg-12 col-md mb-3">
              <input type="text" placeholder="Last Name" id="input-LastName" className="shadow form-control form-control-lg" />
            </div>
            <div className="col-lg-12 mb-3">
              <input type="email" placeholder="email address" id="input-Email" className="shadow form-control form-control-lg" autoComplete="email" value={email} onChange={(e) => setEmail(e.target.value)} />
            </div>
            <div className="col-lg-12 mb-3">
          <input
            type="password"
            placeholder="password"
            id="input-Password"
            className="shadow form-control form-control-lg"
            autoComplete="new-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            onKeyDown={handlePasswordInputKeyDown} // Add this line
          />
        </div>
           
            <div className="col-lg-12 col-md mb-3" style={{ paddingBottom: '15px' }}>
              <button type="button" className="btn btn-primary rounded-pill pt-2 pb-2" style={{ width: 'calc(100% - 1.5rem)' }} onClick={handleSignUp} id="btnSignup">
                submit
                
                <i className="fas fa-paper-plane"></i>
              </button>
            
            </div>
            <div>
            {error && <p style={{ color: 'red' }}>{error}</p>}
            </div>
          
          </div>
        </form>
        </div>

</div>
    );
    }



export default SignUpBox;