import React, { useState } from 'react';
import { auth } from '../firebase';
import { signInWithEmailAndPassword } from 'firebase/auth';



function EmailBox({ onGoBack, showPasswordBox, onLoginSuccess }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);

  const handleForgotPasswordClick = () => {
    showPasswordBox(); // Call the passed function to open the password reset popup
  };

  const handleSignIn = () => {
    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log('Signed in successfully:', user);

        if (onLoginSuccess) {
          onLoginSuccess();
        }
      })
      .catch((error) => {
        // Handle sign-in error
        setError('Login failed. Please check your email and password.');
      });
  };

  const handleLogout = () => {
    auth
      .signOut()
      .then(() => {
        console.log('Signed out successfully');
      })
      .catch((error) => {
        console.error('Error signing out:', error);
      });
  };

  const handlePasswordInputKeyDown = (event) => {
    if (event.key === 'Enter') {
      // If Enter key is pressed in the password input field, submit the form
      handleSignIn();
    }
  };

  return (
    <div className="login-box" id="loginBox">  
        <div className="log-in-text">
          <div className="opening-second">
            <div className="go-back">
              <button className="go-back-button" onClick={onGoBack}>
                <i className="fas fa-backspace"></i>
              </button>
              <div className="center-text">
                <h4 className="log-in-text-heading">Sign-In</h4>
              </div>
            </div>
          </div>
          <h6 className="log-in-text-closing">
            Let's sign you up so you can get the answers you need!
          </h6>
        </div>
        <div className="signup-form">
          <div className="col-lg-12 mb-3">
            <input
              type="email"
              placeholder="email"
              id="input-user-Email"
              className="shadow form-control form-control-lg"
              autoComplete="email"
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="col-lg-12 mb-3">
        <input
          type="password"
          placeholder="password"
          id="input-user-Password"
          className="shadow form-control form-control-lg"
          autoComplete="new-password"
          onChange={(e) => setPassword(e.target.value)}
          onKeyDown={handlePasswordInputKeyDown} // Add this line
        />
      </div>

          <div className="col-lg-12 col-md mb-3" style={{ marginTop: '15px' }}>
            <button
              type="submit"
              className="btn btn-primary rounded-pill pt-2 pb-2"
              style={{ width: 'calc(100% - 1.5rem)' }}
              id="btnLogin"
              onClick={handleSignIn}
  
            >
              submit
              <i className="fas fa-paper-plane"></i>
            </button>
          </div>
          <div className="col-lg-12 col-md mb-3">
            <button
              type="submit"
              className="btn btn-primary rounded-pill pt-2 pb-2"
              style={{ width: 'calc(100% - 1.5rem)' }}
              id="btnLogout"
              onClick={handleLogout}  
           
            >
              Logout
            </button>
       
          </div>  
          <div >
          {error && <p style={{ color: 'red' }}>{error}</p>}
       
          </div>

          {/* Clickable link for forgotten password */}
          <div className="col-lg-12 col-md mb-3">
            <button
              id="forgotPassword"
              className="signup-button"
              type="button"
              onClick={handleForgotPasswordClick}
            >
              
              Forgot your password?
            </button>
          </div>
        </div>
      </div>

  );
}

export default EmailBox;
    