import React, { useState } from 'react';
import { auth, googleProvider, facebookProvider } from '../firebase';
import { signInWithRedirect, signInWithEmailAndPassword } from 'firebase/auth';

function LoginBox(props, onLoginSuccess) {

const { showPasswordBox } = props;
const [email, setEmail] = useState('');
const [password, setPassword] = useState('');
const [error, setError] = useState(null);

const handleForgotPasswordClick = () => {
  showPasswordBox(); // Call the passed function to open the password reset popup
};

const handleSignIn = () => {
signInWithEmailAndPassword(auth, email, password)
.then((userCredential) => {
const user = userCredential.user;
console.log('Signed in successfully:', user);

if (onLoginSuccess) {
onLoginSuccess();
}
})
.catch((error) => {
// Handle sign-in error
setError('Forgot your password or email? Click here');
});
};

const handleLogout = () => {
auth
.signOut()
.then(() => {
console.log('Signed out successfully');
})
.catch((error) => {
console.error('Error signing out:', error);
});
};

const handlePasswordInputKeyDown = (event) => {
if (event.key === 'Enter') {
// If Enter key is pressed in the password input field, submit the form
handleSignIn();
}
};


const signInWithGoogle = async () => {
  try {
    const userCredential = await signInWithRedirect(auth, googleProvider);
    const user = userCredential.user;
    
    // Access the user's first name (assuming it's available in the user's profile)
    const firstName = user.displayName.split(' ')[0]; // Assuming the first name is the first part of the display name

    // Now you can use the firstName variable to display the user's first name
    console.log('Signed in successfully with Google:', firstName);

  } catch (err) {
    console.error(err);
  }
};
const signInWithFacebook = async () => {
try {
await signInWithRedirect(auth, facebookProvider);
} catch (err) {
console.error(err);
}
};

return (
  
<div className="login-box" id="loginBox"> 
  <div className="log-in-text">
    <div className="log-in-text-opening">
      {/* Main heading */}
      <h4 className="log-in-text-opening">Log in or sign up in seconds</h4>
    </div>
    {/* Subheading */}
    <h6 className="log-in-text-closing">
      <p className="fw-light">Log in with your email or another service to continue with TioAssist.io</p>
    </h6>
  </div>


{/* Social media buttons section */}
<div className="social-login">
{/* Google sign-in button */}
<button id="google-login" className="social-button google" onClick={signInWithGoogle} type="button">
<span className="text" style={{ fontWeight: '500' }}>Continue with Google</span>
<span className="icon">
{/* Google icon */}
<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" height="35" width="35">
<path fill="#4285f4" d="M386 400c45-42 65-112 53-179H260v74h102c-4 24-18 44-38 57z"></path>
<path fill="#34a853" d="M90 341a192 192 0 0 0 296 59l-62-48c-53 35-141 22-171-60z"></path>
<path fill="#fbbc02" d="M153 292c-8-25-8-48 0-73l-63-49c-23 46-30 111 0 171z"></path>
<path fill="#ea4335" d="M153 219c22-69 116-109 179-50l55-54c-78-75-230-72-297 55z"></path>
</svg>
</span>
</button>

{/* Facebook sign-in button */}
<button id="facebook-login" className="social-button facebook" onClick={signInWithFacebook}type="button">
<span className="icon">
{/* Facebook icon */}
<svg fill="none" height="35" viewBox="0 0 24 24" width="35" xmlns="http://www.w3.org/2000/svg">
<path d="M21.5 12.058c0-5.28-4.253-9.558-9.5-9.558s-9.5 4.279-9.5 9.558c0 4.771 3.473 8.725 8.016 9.442v-6.68H8.104v-2.762h2.412V9.952c0-2.395 1.417-3.718 3.588-3.718 1.04 0 2.126.186 2.126.186v2.352h-1.198c-1.18 0-1.548.738-1.548 1.494v1.792h2.635l-.421 2.763h-2.214V21.5c4.543-.717 8.016-4.67 8.016-9.442z" fill="#3b5998"></path>
</svg> 
</span>
<span className="text" style={{ fontWeight: '500' }}>Continue with Facebook</span>
</button>

{/* " sign up" fields */} 
<div className="signupForm">
<div className="col-lg-12 mb-3">
  <input
    type="email"
    placeholder="email"
    id="input-user-Email"
    className="shadow form-control form-control-lg"
    autoComplete="email"
    onChange={(e) => setEmail(e.target.value)}
    style={{ fontSize: '1rem' }} // Set the font size inline
  />
</div>
<div className="col-lg-12 mb-3">
  <input
    type="password"
    placeholder="password"
    id="input-user-Password"
    className="shadow form-control form-control-lg"
    autoComplete="new-password"
    onChange={(e) => setPassword(e.target.value)}
    onKeyDown={handlePasswordInputKeyDown}
    style={{ fontSize: '1rem' }} // Set the font size inline
  />
</div>
</div>


<div className="row custom-row"> {/* Add the custom-row class here */}
  <div className="col-lg-6 col-md-12 ">

    <button
      id="signInButton"
      className="signup-button"
      type="button"
      onClick={handleSignIn}
    >
      Log in
    </button>
  </div>

  <div className="col-lg-6 col-md-12 ">
    <button
      id="forgotPasswordButton"
      className="signup-button"
      type="button"
      onClick={props.showSignUpBox} 
    >
      New User?
    </button>
  </div>
</div>


{/* "Continue sign up" button */} 

</div>

<div className="terms">
<p className="fw-light">
By continuing, you agree to our <a href="URL_TO_TERMS_OF_USE" target="_blank" rel="noopener">Terms of Use</a>.
Read our <a href="URL_TO_PRIVACY_POLICY" target="_blank" rel="noopener">Privacy Policy</a>.
</p>
</div>
<div>
  {error && (
    <p className="fw-light" style={{ color: 'red' }}>
      <a href="#" onClick={handleForgotPasswordClick} style={{ color: 'red', textDecoration: 'none' }}>
        {error}
      </a>
    </p>
  )}
</div>

</div>

);
}

export default LoginBox;

